import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Radio from '@material-ui/core/Radio';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import WcIcon from '@material-ui/icons/Wc';
import RestaurantIcon from '@material-ui/icons/Restaurant';
// import HotelIcon from '@material-ui/icons/Hotel';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
// import ApartmentIcon from '@material-ui/icons/Apartment';
import NotListedLocationIcon from '@material-ui/icons/NotListedLocation';
import InputAdornment from '@material-ui/core/InputAdornment';
import AccessibleIcon from '@material-ui/icons/Accessible';
import ChildFriendlyIcon from '@material-ui/icons/ChildFriendly';
import theme from '../theme';
import locationsService from '../services/locations';
import '../App.css';

const NewLocationForm = ({
  newLocationCoords,
  toggleAddNewForm,
  buttonLabelAdd,
  buttonLabelCancel,
  removePlaceholderLocation,
  handleErrorNotifications,
  logOut,
  resetNewLocationCoords,
}) => {
  const [servicesChecked, setServicesChecked] = useState({
    inva: false,
    child: false,
  });
  const [location, setLocation] = useState({
    name: '',
    type: '',
    payable: '',
    services: [],
    validated: false,
    lat: newLocationCoords.lat,
    lng: newLocationCoords.lng,
    rating: 3,
    ratings: 0,
  });

  const styles = {
    container: {
      backgroundColor: '#fff',
      padding: '.5rem',
      height: '50vh',
      position: 'absolute',
      bottom: 0,
      left: 0,
      zIndex: 3,
    },
  };

  const handleNewLocation = (e) => {
    const { name, value } = e.target;

    if (value === 'inva') {
      setServicesChecked({ ...servicesChecked, inva: !servicesChecked.inva });
    }

    if (value === 'child') {
      setServicesChecked({ ...servicesChecked, child: !servicesChecked.child });
    }

    if (value === 'inva' && !location.services.includes('inva')) {
      setLocation({
        ...location,
        services: location.services.concat(value),
      });
    }

    if (value === 'inva' && location.services.includes('inva')) {
      setLocation({
        ...location,
        services: location.services.filter((service) => service !== value),
      });
    }

    if (value === 'child' && !location.services.includes('child')) {
      setLocation({
        ...location,
        services: location.services.concat(value),
      });
    }

    if (value === 'child' && location.services.includes('child')) {
      setLocation({
        ...location,
        services: location.services.filter((service) => service !== value),
      });
    }

    if (name !== 'service') {
      setLocation({
        ...location,
        [name]: value,
      });
    }
  };

  const addLocation = async (e) => {
    console.log('LOG');
    e.preventDefault();
    try {
      await locationsService.createNewLocation(location);
      toggleAddNewForm();
    } catch (error) {
      console.log(error.response.data);
      removePlaceholderLocation({ lat: location.lat, lng: location.lng });
      toggleAddNewForm();
      handleErrorNotifications(
        'Istuntosi on vanhentunut. Kirjaudu sisään lisätäksesi vessan.',
        true
      );
      logOut();
    }
  };

  const resetNewLocation = () => {
    removePlaceholderLocation({ lat: location.lat, lng: location.lng });
    setLocation({
      name: '',
      type: '',
      payable: '',
      services: [],
      validated: false,
      lat: newLocationCoords.lat,
      lng: newLocationCoords.lng,
      rating: 3,
      ratings: 0,
    });
    toggleAddNewForm();
    resetNewLocationCoords();
  };

  const useStyles = makeStyles(() => ({
    paper: {
      marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    form: {
      width: '100%',
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    button: {
      marginTop: theme.spacing(2),
      width: '100%',
    },
    margin: {
      marginTop: theme.spacing(2),
    },
    smallMargin: {
      marginTop: theme.spacing(1),
    },
    formControl: {
      width: '100%',
    },
  }));

  const classes = useStyles();

  return (
    <div className="infobar-container" style={styles.container}>
      <form onSubmit={addLocation}>
        <Grid container>
          <ThemeProvider theme={theme}>
            <Grid item xs={12}>
              <FormLabel
                style={{
                  fontSize: '1.2rem',
                  fontWeight: '300',
                  padding: '.5rem',
                }}
              >
                Lisää kohde
              </FormLabel>
            </Grid>
            <Grid item xs={12}>
              <TextField
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <WcIcon style={{ color: '#717CB9' }} />
                    </InputAdornment>
                  ),
                }}
                variant="outlined"
                margin="normal"
                size="small"
                color="primary"
                required
                fullWidth
                name="name"
                label="Nimi"
                value={location.name}
                onChange={(e) => handleNewLocation(e)}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl variant="outlined" className={classes.formControl}>
                <InputLabel htmlFor="new-location-type">Tyyppi</InputLabel>
                <Select
                  native
                  value={location.type}
                  onChange={(e) => handleNewLocation(e)}
                  label="Tyyppi"
                  inputProps={{
                    name: 'type',
                    id: 'new-location-type',
                  }}
                >
                  <option aria-label="None" value="" disabled hidden />
                  <option value="public">Julkinen</option>
                  <option value="restaurant">Ravintola</option>
                  <option value="cafe">Kahvila</option>
                  <option value="other">Muu</option>
                </Select>
              </FormControl>
            </Grid>
            <Grid container>
              <Grid item xs={6} className={classes.smallMargin}>
                <FormLabel component="legend" style={{ marginTop: '.2rem' }}>
                  Lisäpalvelut
                </FormLabel>
              </Grid>
              <Grid item xs={6} className={classes.smallMargin}>
                <FormLabel component="legend" style={{ marginTop: '.2rem' }}>
                  Maksullinen
                </FormLabel>
              </Grid>
            </Grid>
            <Grid item xs={6}>
              <FormGroup>
                <Grid container>
                  <FormControlLabel
                    control={
                      <Checkbox
                        name="service"
                        value="inva"
                        checked={servicesChecked.inva}
                        onChange={(e) => handleNewLocation(e)}
                      />
                    }
                    label={<AccessibleIcon style={{ color: '#717CB9' }} />}
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        name="service"
                        value="child"
                        checked={servicesChecked.child}
                        onChange={(e) => handleNewLocation(e)}
                      />
                    }
                    label={<ChildFriendlyIcon style={{ color: '#717CB9' }} />}
                  />
                </Grid>
              </FormGroup>
            </Grid>
            <Grid item xs={6}>
              <RadioGroup>
                <Grid container>
                  <FormControlLabel
                    control={
                      <Radio
                        name="payable"
                        value="payable"
                        checked={location.payable === 'payable'}
                        onChange={(e) => handleNewLocation(e)}
                      />
                    }
                    label="Kyllä"
                    style={{ fontWeight: 300 }}
                  />
                  <FormControlLabel
                    control={
                      <Radio
                        name="payable"
                        value="free"
                        checked={location.payable === 'free'}
                        onChange={(e) => handleNewLocation(e)}
                      />
                    }
                    label="Ei"
                    style={{ fontWeight: 300 }}
                  />
                </Grid>
              </RadioGroup>
            </Grid>
            <Grid item xs={12}>
              <Grid container justify="space-around" alignItems="flex-end">
                <Grid item xs={4}>
                  <Button
                    variant="contained"
                    color="primary"
                    type="submit"
                    className={classes.button}
                  >
                    {buttonLabelAdd}
                  </Button>
                </Grid>
                <Grid item xs={4}>
                  <Button
                    variant="contained"
                    color="primary"
                    className={classes.button}
                    onClick={resetNewLocation}
                  >
                    {buttonLabelCancel}
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          </ThemeProvider>
        </Grid>
      </form>
    </div>
  );
};

NewLocationForm.propTypes = {
  newLocationCoords: PropTypes.instanceOf(Object).isRequired,
  toggleAddNewForm: PropTypes.func.isRequired,
  buttonLabelAdd: PropTypes.string.isRequired,
  buttonLabelCancel: PropTypes.string.isRequired,
  removePlaceholderLocation: PropTypes.func.isRequired,
  handleErrorNotifications: PropTypes.func.isRequired,
  logOut: PropTypes.func.isRequired,
  resetNewLocationCoords: PropTypes.func.isRequired,
};

export default NewLocationForm;
